var isLoding = false;
export default {
	data() {
		return {
			status: false,
		};
	},
	methods: {
		onBackPress(e) {
			// return true 表示禁止默认返回
			console.log("监听到返回")
			return false
		},
		
		/**
		 * 微信小程序登陆
		 */
		getUserInfo(e) {
			// console.log("getting UserInfo...")
			// console.log(e);
			
			// 加锁
			if (isLoding) {
				return;
			}
			isLoding = true;

			uni.showLoading({
				title: '载入中...',
			});
			setTimeout(() => {
				if (isLoding) {
					isLoding = false // 解锁
					uni.hideLoading();
					uni.showToast({
						title: "网络未知错误",
						icon: "none",
						duration: 1000
					})
				}
			}, 5000); // 延时5s timeout
			
			var info = e.detail;
			var that = this;
			uni.login({
				success: res_login => {
					// console.log('-------res_login，获取code-------')
					// console.log(res_login);
					// 后端获取openid 并设置用户信息
					uni.request({
						url: that.$serverUrl + '/user/getWxUserInfo',
						method: "POST",
						data: {
							encryptedData: info.encryptedData,
							iv: info.iv,
							code: res_login.code,

							nickname: info.userInfo.nickName,
							faceImg: info.userInfo.avatarUrl
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						success: (res) => {
							// console.log(res)
							if (res.data.status == 200) {
								// 3.获取返回的用户信息
								var finalUser = res.data.data;
								// 4.分割邮箱地址, 重构 user
								finalUser = this.myUser(finalUser);
								// 5.写入缓存
								this.setGlobalUserInfo(finalUser);
								console.log(finalUser);

								// 6.到主页
								isLoding = false;
								uni.hideLoading();
								uni.redirectTo({
									url: "../index/index"
								});
							}
						}
					});
				}, // end of login success
				fail: (res) => {
					isLoding = false;
					uni.hideLoading();
					uni.showToast({
						title: "login fail",
						icon: "none",
						duration: 1000
					})
				}
				
			});
		},

		changestatus() {
			this.status = !this.status;
		},
	}
};
